<template>
  <div class="ease-sku">
    <sku-group
      v-for="(item, index) in data"
      :key="index"
      :index="index"
      :sku="item"
      :skuTree="skuTreeData"
      :onSkuChange="rebuildSku"
      :onSkuRemove="handleSkuRemove"/>
    <sku-button v-if="data.length < maxSize" :onClick="addSku" />
  </div>
</template>

<script>
import SKUGroup from './components/SKUGroup'
import SKUButton from './components/SKUButton'
import { Message } from 'element-ui'

const noop = res => res
const noopPromise = () => Promise.resolve(noop)

export default {
  name: 'vue-sku',

  provide () {
    return {
      'ease': this
    }
  },

  data () {
    return {
      data: this.$props.value,
      skuTreeData: this.$props.skuTree
    }
  },

  watch: {
    data (value) {
      this.$emit('input', value)
    },

    skuTree (skuTree) {
      this.skuTreeData = skuTree
    }
  },

  props: {
    maxSize: {
      type: Number,
      default: 3
    },
    showAddSkuImage: {
      type: Boolean,
      default: true
    },
    value: {
      type: Array,
      default () {
        return []
      }
    },
    action: {
      type: String,
      default: ''
    },
    headers: {
      type: Object,
      default () {
        return {}
      }
    },
    accept: {
      type: String,
      default: ''
    },
    uploadName: {
      type: String,
      default: 'resource'
    },
    // 
    skuTree: {
      type: Array,
      default () {
        return []
      }
    },
    sku: {
      type: Object,
      default () {
        return {}
      }
    },
    // skuid key
    optionValue: {
      type: String,
      default: 'id'
    },
    // skutext key
    optionText: {
      type: String,
      default: 'text'
    },
    // 
    onFetchGroup: {
      type: Function,
      default: noopPromise
    },
    // 
    onFetchSku: {
      type: Function,
      default: noopPromise
    },
    // 
    onCreateGroup: {
      type: Function,
      default: noopPromise
    },
    // 
    onCreateSku: {
      type: Function,
      default: noopPromise
    }
  },

  components: {
    'sku-group': SKUGroup,
    'sku-button': SKUButton
  },

  methods: {
    addSku () {
      this.data.push({
        leaf: []
      })
    },

    rebuildSku (sku, index) {
      let { skuTreeData, optionValue } = this

      if (
        this.data.some(
          (item, idx) => item[optionValue] === sku[optionValue] && index !== idx
        )
      ) {
        Message.error('')
        return false
      }

      this.$set(this.data, index, Object.assign({}, sku))

      if (!skuTreeData.some(item => item[optionValue] === sku[optionValue])) {
        skuTreeData.push(sku)
      }

      this.$emit('onChange', this.data)
    },

    handleSkuRemove (index) {
      let { data } = this
      data.splice(index, 1)
    }
  },

  beforeMount () {
    let { onFetchGroup } = this
    if (typeof (onFetchGroup) === 'function') {
      onFetchGroup().then(skuTree => {
        this.skuTreeData = skuTree
      })
    }
  }
}
</script>

<style lang="scss">
.ease-sku {
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding: 10px;
  border: 1px solid #e5e5e5;
}
</style>
